.game_power_bar{
  padding: 0 0 0 0;
  display: block;
  margin: 0 auto 0 auto;
  -webkit-appearance: none;
}

.game_power_bar meter, .dismount_game_power_bar meter {
  background: none;
  width: 75%;
  height: 25px;
  box-sizing: border-box;
  padding: 0 0 0 0;
  display: block;
  margin: 0 auto 0 auto;
}

.game_power_bar meter::-webkit-meter-bar, .dismount_game_power_bar meter::-webkit-meter-bar {
  background: #000;
  border-radius: 5px;
  border: 1px solid grey;
}

.game_power_bar meter::-webkit-meter-optimum-value, .dismount_game_power_bar meter::-webkit-meter-optimum-value {
  width: 100%;
  background: linear-gradient(
    90deg,
    #FFC300 0%,
    #FF5733 100%
  );
  background-size: 100% 100%;
  border-radius: 5px;
  transition: background 1s 1s ease-in-out;
}

.game_power_bar meter::-webkit-meter-suboptimum-value, .dismount_game_power_bar meter::-webkit-meter-suboptimum-value {
  width: 100%;
  background: linear-gradient(
    90deg,
    #FFC300 0%,
    #FF5733 100%,
    #7A0000 100%
  );
  background-size: 100% 100%;
  border-radius: 5px;
  transition: background 1s 1s ease-in-out;
}

